import { useState, useEffect, useContext } from 'react';
import { NavLink as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import { SidebarContext } from 'src/contexts/SidebarContext';

import PropTypes from 'prop-types';
import {
  Button,
  Tooltip,
  Badge,
  Collapse,
  ListItem,
  styled,
  tooltipClasses,
  Box
} from '@mui/material';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import ExpandLessTwoToneIcon from '@mui/icons-material/ExpandLessTwoTone';
import ExpandMoreTwoToneIcon from '@mui/icons-material/ExpandMoreTwoTone';
import { getProjectList } from 'src/slices/ProjectList';
import { getTeams } from 'src/slices/team';

const TooltipWrapper = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: theme.colors.alpha.black[100],
    color: theme.palette.getContrastText(theme.colors.alpha.gray[100]),
    fontSize: theme.typography.pxToRem(12),
    fontWeight: 'bold',
    borderRadius: theme.general.borderRadiusSm,
    boxShadow:
      '0 .2rem .8rem rgba(7,9,25,.18), 0 .08rem .15rem rgba(7,9,25,.15)'
  },
  [`& .${tooltipClasses.arrow}`]: {
    color: theme.colors.alpha.black[100]
  }
}));

const SidebarMenuItem = ({
  children,
  link,
  icon: Icon,
  badge,
  badgeTooltip,
  open: openParent,
  active,
  name,
  altName = '',
  ...rest
}) => {
  const [menuToggle, setMenuToggle] = useState(true);
  const { t } = useTranslation();
  const { closeSidebar } = useContext(SidebarContext);
  const dispatch = useDispatch()

  const toggleMenu = () => {
    setMenuToggle((Open) => !Open);
  };

  const projectListData = useSelector((state) => state.projectList?.projectListData);
  const teamList = useSelector((state) => state.team.teamList);
  useEffect(() => {
    dispatch(getProjectList());
    dispatch(getTeams());
  }, [])
  if (children) {
    return (
      <ListItem component="div" className="Mui-children" key={name} {...rest}>
        <Button
          activeClassName={clsx({ 'Mui-active': menuToggle })}
          startIcon={Icon && <Icon />}
          endIcon={
            menuToggle ? <ExpandLessTwoToneIcon /> : <ExpandMoreTwoToneIcon />
          }
          onClick={toggleMenu}
        >
          {badgeTooltip ? (
            <TooltipWrapper title={badgeTooltip} arrow placement="right">
              {badge === '' ? (
                <Badge color="primary" variant="dot" />
              ) : (
                <Badge badgeContent={badge} />
              )}
            </TooltipWrapper>
          ) : badge === '' ? (
            <Badge color="primary" variant="dot" />
          ) : (
            <Badge badgeContent={badge} />
          )}
          {menuToggle ? t(altName) : t(name)}
        </Button>
        <Collapse in={menuToggle}>{children}</Collapse>
      </ListItem>
    );
  }
  return (
    <ListItem component="div" key={name} {...rest} style={{ boxSizing: 'border-box', borderBottom: '1px dashed #CEDBEF' }}>
      {
        (name === 'News Center' || name === 'Circle Rate' || name === 'Govt Policies' || name === 'Sample Docs' || name === 'Deed Writers' || name === 'Maps' || name === 'Bank & DSAs')
          ?
          <Button
            disableRipple
            activeClassName="Mui-active"
            component={RouterLink}
            onClick={closeSidebar}
            to={link}
            startIcon={Icon && <Icon />}

          >
            {t(name)}
            {badgeTooltip ? (
              <TooltipWrapper title={badgeTooltip} arrow placement="right">
                {badge === '' ? (
                  <Badge color="primary" variant="dot" />
                ) : (
                  <Badge badgeContent={badge} />
                )}
              </TooltipWrapper>
            ) : badge === '' ? (
              <Badge color="primary" variant="dot" />
            ) : (
              <Badge badgeContent={badge} />
            )}
          </Button>
          :
          <Button
            disabled={
              name !== 'DASHBOARD' &&
              !(
                (projectListData.length > 0 || teamList.length > 0)
                // (projectListData.length === 0 && teamList.length === 0)
              )
            }
            disableRipple
            activeClassName="Mui-active"
            component={RouterLink}
            onClick={closeSidebar}
            to={link}

            // startIcon={Icon && <Icon style={{ color: (active || rest.disabled) ? 'black' : 'gray' }} />}
            startIcon={
              Icon && (
                <Icon
                  style={{
                    color: projectListData.length > 0 || teamList.length > 0 ? undefined : (active || rest.disabled) ? 'black' : 'gray'
                  }}
                />
              )
            }
          >
            {/* {t(name)}  */}
            <Box style={{ color: projectListData.length > 0 || teamList.length > 0 ? undefined : (active || rest.disabled) ? 'black' : 'gray' }}>{t(name)}</Box>

            {badgeTooltip ? (
              <TooltipWrapper title={badgeTooltip} arrow placement="right">
                {badge === '' ? (
                  <Badge color="primary" variant="dot" />
                ) : (
                  <Badge badgeContent={badge} />
                )}
              </TooltipWrapper>
            ) : badge === '' ? (
              <Badge color="primary" variant="dot" />
            ) : (
              <Badge badgeContent={badge} />
            )}
          </Button>
      }
    </ListItem>
  );
};

SidebarMenuItem.propTypes = {
  children: PropTypes.node,
  active: PropTypes.bool,
  link: PropTypes.string,
  icon: PropTypes.elementType,
  badge: PropTypes.string,
  badgeTooltip: PropTypes.string,
  open: PropTypes.bool,
  name: PropTypes.string.isRequired
};

SidebarMenuItem.defaultProps = {
  open: false,
  active: false
};

export default SidebarMenuItem;
